@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400&display=swap);
.main-content {
  border: 0.15vw solid #969696;
  background-color: #070726;
  
}

.who-text {
  padding: 2.5vw 10vw;
  white-space: nowrap;
}

.name {
  font-size: 4vw;
  font-weight: normal;
  margin-bottom: 0.75vw;
  font-family: 'Noto Sans', sans-serif;
}

.titles {
  font-size: 2.5vw;
  color: #DFDFDF;
  font-family: 'Noto Sans', sans-serif;
}
.contacts {
    display: flex;
    justify-content: center;
    margin-top: 2vw;
}

.logo-img {
    width: 4vw;
}

.logo {
    margin-right: 2vw;
}

.logo:last-child {
    margin-right: 0;
}
.card-container {
  margin-right: 2vw;
  width: 100%;
}

.card-container:last-child {
  margin-right: 0;
}

.card {
  border: 0.15vw solid #969696;
  background-color: #070726;
  padding: 0.75vw 0;
}

.card:hover {
  background-color: #030318;
}


.card-text {
  font-family: 'Roboto', sans-serif;
  font-size: 2.25vw;
  color: #DFDFDF;
}

/* STYLING MODAL COMPONENT */

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal {
  margin: 10vw;
}

/* STYLING MY MOADL PAGE */

.modal-page {
  position: fixed;
  top: 50vh;
  left: 50vw;
  width: 64vw;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #070726;
  border: 0.1vw solid #E2E2E2;
}

.modal-page-content {
  margin: 1.5vw 5vw;
}

.header {
  display: flex;
  justify-content: space-between;
  border-bottom: #E2E2E2 solid 0.15vw;
  padding-bottom: 1.5vw;
  font-size: 2.5vw;
  font-family: 'Noto Sans', sans-serif;
}

.title {
  color: #BF9300;
}

.back {
  color: #939393;
}

.back:hover {
  cursor: pointer;
  color: #E2E2E2;
}

/* MODAL ITEMS */

.project {
  border-bottom: #E2E2E2 solid 0.1vw;
  padding-bottom: 1vw;
}

.project:last-child {
  border: none;
}

.project-title {
  margin-top: 0.75vw;
  font-family: 'Noto Sans', sans-serif;
  font-size: 2vw;
  margin-bottom: 0.5vw;
}

.project-role {
  font-family: 'Roboto', sans-serif;
  font-weight: lighter;
  color: #898989;
  font-size: 1.15vw;
}

.project-link {
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
  font-size: 1.25vw;
  font-weight: lighter;
}
* {
  margin: 0;
  padding: 0;
  color: #FFF;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.center-content {
  position: absolute;
  top: 50vh;
  left: 50vw;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  margin-top: -1%;
}

.background {
  width: 100%;
  height: 100%; 
  position: fixed; 
  background: linear-gradient(220deg, #c2064b, #bb9200);
}

.avatar {
  -webkit-transform: translate(0, 20%);
          transform: translate(0, 20%);
  border-radius: 50%;
  height: 13vw;
}

.info-cards {
  display: flex;
  margin-top: 2vw;
}

