@import url('https://fonts.googleapis.com/css?family=Noto+Sans&display=swap');
@import url('https://fonts.googleapis.com/css?family=Roboto:300,400&display=swap');

* {
  margin: 0;
  padding: 0;
  color: #FFF;
  user-select: none;
}

.center-content {
  position: absolute;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
  text-align: center;
  margin-top: -1%;
}

.background {
  width: 100%;
  height: 100%; 
  position: fixed; 
  background: linear-gradient(220deg, #c2064b, #bb9200);
}

.avatar {
  transform: translate(0, 20%);
  border-radius: 50%;
  height: 13vw;
}

.info-cards {
  display: flex;
  margin-top: 2vw;
}
